body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* background: url(https://images.pexels.com/photos/14676/pexels-photo-14676.png?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left;
    background-attachment: fixed; */
    background: linear-gradient(-135deg, teal, rgb(64, 88, 175));
    height: 100vh;
    width: 100vw;
}

code {
    font-family: Roboto,
        monospace;
}

.topNav {
    position: fixed;
    box-sizing: border-box;
    width: 100vw;
    display: flex;
    justify-content: flex-end;
    padding: 10px 15px;
}

.about, .home, .media, .merch {
    width: 100%;
    text-decoration: none;
    text-align: center;
}

.homePage {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.homePageContainer {
    width: 90vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    opacity: .8;
}

.bellflowerLogo {
    -webkit-filter: invert(100);
            filter: invert(100);
    width: 70%;
    min-width: 330px;
    max-width: 670px;
}

.bandDescription p {
    width: 100%;
    color: white;
    line-height: 22px;
    margin: 15px 0 0 0;
    /* font-size: calc(16px + 6 * ((100vw - 400px) / 380)); */
    font-size: 24px;
}

/* .menuGradient>div>ul {
    background: linear-gradient(90deg, teal, #4058af)!important;
} */

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

